@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.App {
  text-align: center;
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Sample-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  z-index: 0;
}

/* Title */
.App-title {
  position: relative;
  z-index: 3;
  color: white;
  font-size: 7rem;
  text-align: center;
  text-align: center;
  font-family: 'Helvetica', 'Arial', 'Futura', sans-serif;
  letter-spacing: 0.1em; /* Optional: Adjust spacing to match the style */
  transform: scaleY(1.15); /* Stretches text vertically */
}


.text-transition-container {
  position: relative; /* Ensure child elements are positioned relative to this container */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Hide overflowing text during animation */
  width: 100%; /* Ensure it spans the container width */
  height: 100px; /* Set height to prevent layout shifting */
  z-index: 3;
  color: white;
  font-family: 'Poppins', sans-serif; /* Light, geometric font */
  font-weight: 200; /* Thin weight */
  letter-spacing: 0.2em; /* Add spacing for a clean style */
}

.text-transition {
  opacity: 0; /* Start invisible */
  font-size: 2rem; /* Adjust font size */
  color: white;
  margin: 0 10px; /* Add spacing between items */
  transform: translateY(20px); /* Start below the container */
  animation-fill-mode: forwards; /* Ensures the animation's final state persists */
}

.text-transition:nth-child(1) {
  animation: fadeInStay 2s forwards; /* Ensure the animation ends with the final state */
  animation-delay: 0s;
}
.text-transition:nth-child(2) {
  animation: fadeInStay 2s forwards; /* Apply animation-fill-mode here as well */
  animation-delay: 2s;
}
.text-transition:nth-child(3) {
  animation: fadeInStay 2s forwards; /* Same for the third text */
  animation-delay: 4s;
}

@keyframes fadeInStay {
  0% {
    opacity: 0; /* Invisible at the start */
    transform: translateY(20px); /* Positioned below */
  }
  20% {
    opacity: 1; /* Start to fade in */
    transform: translateY(0); /* Slide up to the final position */
  }
  100% {
    opacity: 1; /* Stay visible */
    transform: translateY(0); /* Remain in place */
  }
}




/* Dark overlay */
.dark-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Adjust opacity for desired darkness */
  z-index: 2;
}

/* Filmy Overlay */
.filmy-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    rgba(0, 0, 0, 0.2), 
    rgba(0, 0, 0, 0.4)
  ); /* Gradient for a cinematic look */
  mix-blend-mode: overlay; /* Blend mode to add texture effect */
  z-index: 2;
  pointer-events: none; /* Ensure it doesn't block interactions */
  opacity: 0.8; /* Adjust transparency for effect strength */
}


/* Navigation styling */
.Nav {
  position: absolute;
  top: 20px;
  left: 20px;
  display: flex;
  align-items: center;
  z-index: 10;
}

/* Remove link underline */
.Nav ul {
  list-style: none; /* Remove bullet points */
  padding: 0;
  margin: 0;
}

.Nav li {
  margin: 10px 0; /* Adjust spacing as needed */
}

/* Ensure no underline for links in Nav */
.Nav a {
  text-decoration: none !important; /* Forcefully remove underline */
  color: inherit; /* Use the current text color (inherited from the parent) */
  font-weight: bold; /* Optional: Make text bold */
}

/* Ensure no underline on hover */
.Nav a:hover {
  text-decoration: none !important; /* Force no underline on hover */
  color: inherit; /* Ensure the hover color is the same */
}


.hamburger {
  position: relative;
  width: 30px;
  height: 25px;
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0;
  margin-right: 10px;
}

.hamburger .bar {
  width: 100%;
  height: 4px;
  background-color: #afafaf;
  transition: all 0.3s ease-in-out;
}

.hamburger.open .bar:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.hamburger.open .bar:nth-child(2) {
  opacity: 0;
}

.hamburger.open .bar:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}

.brand-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #afafaf;
}

/* Overlay styling */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  pointer-events: none;
}

.overlay.active {
  opacity: 1;
  pointer-events: auto; /* Restore pointer events when active */
}


.menu {
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  z-index: 2;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.menu.active {
  opacity: 1;
}

.menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.menu li {
  margin: 15px 0;
  font-size: 1.5rem;
  color: #ffffff;
  cursor: pointer;
}

/* About Section */
.About {
  background-color: #f8f8f8;
  color: #333;
  padding: 60px 20px;
  text-align: center;
}

.About h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #111;
}

.About p {
  font-size: 1.25rem;
  line-height: 1.6;
  max-width: 800px;
  margin: 0 auto;
}

/* Projects Section */
.Projects {
  background-color: #111;
  color: #fff;
  padding: 60px 20px;
  text-align: center;
}

.Projects h2 {
  font-size: 2.5rem;
  margin-bottom: 40px;
}

.project-cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.project-card {
  background-color: #333;
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.project-card:hover {
  transform: translateY(-10px);
}

.project-card h3 {
  color: #fff;
  margin-bottom: 10px;
}

.project-card p {
  color: #ccc;
}

/* Testimonials Section */
.Testimonials {
  background-color: #f1f1f1;
  color: #333;
  padding: 60px 20px;
  text-align: center;
}

.Testimonials h2 {
  font-size: 2.5rem;
  margin-bottom: 40px;
}

.testimonials-cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.testimonial-card {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.testimonial-card:hover {
  transform: translateY(-10px);
}

.testimonial-card p {
  font-style: italic;
  margin-bottom: 10px;
}

.testimonial-card span {
  font-weight: bold;
  color: #555;
}

/* Skills section styling */
.Skills {
  padding: 40px 20px;
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
}

.Skills h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #111;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.skill-card {
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  flex: 1 1 calc(50% - 20px); /* Two cards per row, with gap */
}

.skill-card h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #333;
}

.skill-card p {
  font-size: 1rem;
  color: #555;
}

/* Arrow styling */
.scroll-arrow {
  display: block;
  position: absolute;
  bottom: 20px; /* Adjust position above the bottom */
  left: 50%;
  transform: translateX(-50%);
  font-size: 2rem; /* Adjust size for visibility */
  color: white;
  text-decoration: none;
  z-index: 3; /* Ensure it's above the video and overlay */
  animation: bounce 1.5s infinite; /* Add a subtle bounce effect */
  cursor: pointer;
}

.scroll-arrow:hover,
.scroll-arrow:focus,
.scroll-arrow:active {
  text-decoration: none; /* Forcefully ensure no underline */
}

/* Bounce animation for the arrow */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translate(-50%, 0);
  }
  40% {
    transform: translate(-50%, -10px);
  }
  60% {
    transform: translate(-50%, -5px);
  }
}
